<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="菜单名称" :label-width="width">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="上级菜单" label-width="120px">
          <el-select v-model="form.pid" placeholder="请选择活动区域" @change="changePid">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option label="顶级菜单" :value="0"></el-option>
            <el-option v-for="item in list" :key="item.id" :label="item.title" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="菜单类型" :label-width="width">
          <el-radio v-model="form.type" :label="1" disabled>目录</el-radio>
          <el-radio v-model="form.type" :label="2" disabled>菜单</el-radio>
        </el-form-item>
        <el-form-item label="菜单图标" v-if="form.type==1" :label-width="width">
          <el-select v-model="form.icon" placeholder="请选择图标">
            <el-option v-for="item in icons" :key="item" :value="item">
              <i :class="item"></i>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="菜单地址" v-else :label-width="width">
          <el-select v-model="form.url" placeholder="请选择地址">
            <el-option
              v-for="item in indexRouters"
              :key="item.path"
              :label="item.name"
              :value="'/'+item.path"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="状态" :label-width="width">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.isAdd">添加</el-button>
        <el-button type="primary" @click="updata" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { reqMenuAdd } from "../../util/request";
import { alertSuccess, alertWarning } from "../../util/alert";
import { indexRouters } from "../../router/index";
import { reqMenuUpdate, reqMenuListOne } from "../../util/request";
export default {
  props: ["info"],
  computed: {
    ...mapGetters({
      list: "menu/list"
    })
  },
  components: {},
  data() {
    return {
      indexRouters: indexRouters,
      width: "120px",
      form: {
        pid: 0,
        title: "",
        icon: "",
        type: "",
        url: "",
        status: 1
      },
      icons: [
        "el-icon-s-tools",
        "el-icon-message-solid",
        "el-icon-s-check",
        "el-icon-s-data"
      ]
    };
  },
  methods: {
    cancel() {
      this.$emit("cancel");
    },
    changePid() {
      if (this.form.pid === 0) {
        this.form.type = 1;
      } else {
        this.form.type = 2;
      }
    },
    empty() {
      this.form = {
        pid: "",
        title: "",
        icon: "",
        type: 0,
        url: "",
        status: 1
      };
    },
    checked() {
      return new Promise((resolve, reject) => {
        if (this.form.title === "") {
          alertWarning("菜单名称不能为空");
          return;
        }
        if (this.form.pid === "") {
          alertWarning("上级菜单不能为空");
          return;
        }
        if (this.form.type === "1") {
          if (this.form.icon === "") {
            alertWarning("图标不能为空");
            return;
          }
        }
        if (this.form.type === "2") {
          if (this.form.url === "") {
            alertWarning("地址不能为空");
            return;
          }
        }

        resolve();
      });
    },
    add() {
      this.checked().then(() => {
        reqMenuAdd(this.form).then(res => {
          if (res.data.code == 200) {
            alertSuccess(res.data.msg);
            this.cancel(); //通知menu.vue关闭遮罩层
            this.requestMenuList(); //重新渲染菜单列表
            this.empty();
          } else {
            alertWarning(res.data.msg);
          }
        });
      });
    },
    look(id) {
      reqMenuListOne({ id: id }).then(res => {
        this.form = res.data.list;
        this.form.id = id;
      });
    },
    ...mapActions({
      requestMenuList: "menu/requestMenuList" //解构菜单列表的axios数据
    }),
    updata() {
      this.checked().then(() => {
        reqMenuUpdate(this.form).then(res => {
          if (res.data.code == 200) {
            alertSuccess(res.data.msg);
            this.requestMenuList();
            this.cancel();
            this.empty();
          } else {
            alertSuccess(res.data.msg);
          }
        });
      });
    }
  },
  mounted() {
    // reqMenuListOne(this.form.pid).then(res=>{
    //   console.log(res)
    // })
  }
};
</script>
<style scoped>
</style>


