<template>
  <div>
    <el-button type="primary" @click="willadd">添加</el-button>
    <v-add :info="info" @cancel="cancel" ref="add"></v-add>
    <v-list @edit='edit($event)'></v-list>
  </div>
</template>
<script>
import vList from "./list.vue";
import vAdd from "./add.vue";
export default {
  components: {
    vList,
    vAdd
  },
  data() {
    return {
      info: {
        isShow: false,
        title: "菜单添加",
        btn: "添加"
      }
    };
  },
  methods: {
    willadd() {
       this.$refs.add.empty(),
      this.info ={
            isShow:true,
            title:'菜单添加',
            btn:'添加',
            isAdd:true
        } 
        
    },
    cancel() {
      this.info.isShow = false;
    },
    edit(id){
      this.info ={
            isShow:true,
            title:'菜单编辑',
            btn:'修改'
        } 
        this.$refs.add.look(id)
    }
  },
  mounted() {}
};
</script>
<style scoped>
</style>


